.tartib {

    &__item {
        user-select: none;
        box-sizing: border-box;
        touch-action: none;
        

        &--dragged {
            position: fixed;
            pointer-events: none;
            z-index: 1000;
        }
    }

    &--elevation {
        box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.14),
                    0 1px 18px 0 rgba(0, 0, 0, 0.12),
                    0 3px 5px -1px rgba(0, 0, 0, 0.2);
    }

    &__placeholder {
        visibility: hidden;
    }

}